*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto-Regular"), local("Roboto-Regular"), url("../fonts/roboto/Roboto.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Medium"), local("Roboto Medium"), url("../fonts/roboto/Robotomedium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto-Bold"), local("Roboto-Bold"), url("../fonts/roboto/Robotobold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Open Sans';
  src: local("Open Sans Semibold"), local("OpenSans-Semibold"), url("../fonts/opensans/opensanssemibold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

:root {
  --f-roboto: 'Roboto';
  --f-open-sans: 'Open Sans';
  --w-open-sans-sm: 600;
  --w-roboto-m: 500;
  --w-roboto-b: 700;
}

* {
  outline: none;
  border: 0;
}

html, body {
  position: relative;
  overflow-x: hidden;
}

html {
  font-size: 100%;
}

button {
  background-color: transparent;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  position: relative;
  font-family: var(--f-roboto);
  font-size: 1rem;
  font-weight: 400;
  overflow-x: hidden;
}

.relative {
  position: relative;
}

.container {
  width: 1248px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 1360px) {
  .container {
    width: 960px;
  }
}

@media (max-width: 991px) {
  .container {
    width: calc(100% - 32px);
  }
}

@media (max-width: 600px) {
  .container {
    width: calc(100% - 32px);
  }
}

.w-8\/12 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.w-7\/12 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%;
}

.w-6\/12 {
  flex: 0 0 50%;
  max-width: 50%;
}

.w-5\/12 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.w-4\/12 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-col {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.items-center {
  align-items: center !important;
}

.items-start {
  align-items: flex-start !important;
}

.items-end {
  align-items: flex-end !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.review {
  padding: 6.875rem 0;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 991px) {
  .review {
    flex-wrap: wrap;
  }
}

.review_google, .review_yandex, .review_default {
  flex: 1 1 auto;
  align-self: baseline;
  max-width: 25rem;
  margin-right: 1.5rem;
}

@media (max-width: 1360px) {
  .review_google, .review_yandex, .review_default {
    flex: 0 0 calc((100% - 3rem) / 3);
  }
}

@media (max-width: 991px) {
  .review_google, .review_yandex, .review_default {
    flex: 0 0 100%;
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 2rem;
  }
  .review_google:last-child, .review_yandex:last-child, .review_default:last-child {
    margin-bottom: 0;
  }
}

.review_google {
  border: 0.0625rem solid #E8EAED;
  padding: 1.5rem 1rem;
  border-radius: 0.5rem;
}

@media (max-width: 991px) {
  .review_google {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.review_google__header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}

@media (max-width: 991px) {
  .review_google__header {
    margin-bottom: 0;
  }
}

@media (max-width: 600px) {
  .review_google__header {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 1.25rem;
  }
}

.review_google__header img {
  width: 2rem;
  height: 2rem;
  margin-right: 0.875rem;
}

@media (max-width: 991px) {
  .review_google__header img {
    margin-right: 1rem;
  }
}

@media (max-width: 600px) {
  .review_google__header img {
    margin-right: 0.875rem;
  }
}

.review_google__header .text {
  font-size: 1.25rem;
  line-height: 1.5rem;
  letter-spacing: -0.025rem;
}

@media (max-width: 1360px) {
  .review_google__header .text {
    max-width: 13.625rem;
  }
}

@media (max-width: 991px) {
  .review_google__header .text {
    font-size: 1.375rem;
    max-width: 14.375rem;
  }
}

@media (max-width: 600px) {
  .review_google__header .text {
    font-size: 1.25rem;
    max-width: 100%;
  }
}

.review_google__control {
  display: flex;
  flex-direction: column;
}

@media (max-width: 991px) {
  .review_google__control {
    align-items: flex-end;
  }
}

@media (max-width: 600px) {
  .review_google__control {
    flex: 0 0 100%;
    max-width: 100%;
    align-items: flex-start;
  }
}

.review_google__control-top {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 0.75rem;
}

@media (max-width: 991px) {
  .review_google__control-top {
    margin-bottom: 0.25rem;
  }
}

.review_google__control-top span {
  font-weight: var(--w-roboto-m);
  font-size: 1.125rem;
  line-height: 1.5rem;
  letter-spacing: -0.04375rem;
  margin-right: 0.75rem;
}

.review_google__control-top ul {
  display: flex;
  flex-wrap: wrap;
  margin-right: 0.4375rem;
}

.review_google__control-top ul li {
  margin-right: 0.15rem;
}

.review_google__control-top ul li:last-child {
  margin-right: 0;
}

.review_google__control-top a {
  font-family: var(--f-open-sans);
  font-weight: var(--w-open-sans-sm);
  color: #3F87E8;
  font-size: 0.875rem;
  line-height: 1.5rem;
  letter-spacing: -0.04375rem;
}

.review_google__control p {
  font-size: 0.875rem;
  line-height: 1.5rem;
  letter-spacing: -0.0125rem;
  color: #72777C;
}

.review_default {
  border: 0.0625rem solid #E5EAF1;
}

.review_default__top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.review_default__top::before {
  content: "";
  position: absolute;
  bottom: 0.1875rem;
  z-index: -1;
  left: 0;
  width: 100%;
  height: 0.0625rem;
  background-color: #E5EAF1;
}

.review_default__top ul {
  display: flex;
  flex-wrap: wrap;
}

.review_default__top .text {
  margin-left: 1.4375rem;
  margin-right: 2.5rem;
  font-family: Arial;
  font-size: 0.875rem;
  font-weight: bold;
}

@media (max-width: 1360px) {
  .review_default__top .text {
    display: none;
  }
}

.review_default__top .text b {
  color: #8F9BAC;
}

.review_default__top .count {
  margin-right: 0.5rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2ABE64;
  font-family: 'Arial';
  color: white;
  font-size: 0.875rem;
  font-weight: bold;
}

.review_default__bottom {
  padding: 1rem;
}

.review_default__bottom a {
  font-family: 'Arial';
  font-weight: bold;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  color: #2F353F;
}

.review_default__bottom a span {
  color: #8F9BAC;
}

.review_yandex {
  border-radius: 0.875rem;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

@media (max-width: 1360px) {
  .review_yandex {
    padding: 1.5rem 1.125rem;
  }
}

@media (max-width: 991px) {
  .review_yandex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media (max-width: 600px) {
  .review_yandex {
    padding: 1rem;
  }
}

.review_yandex__header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 1rem;
}

@media (max-width: 1360px) {
  .review_yandex__header {
    align-items: flex-start;
  }
}

@media (max-width: 991px) {
  .review_yandex__header {
    flex: 0 0 50%;
    max-width: 50%;
    margin-bottom: 0;
  }
}

@media (max-width: 600px) {
  .review_yandex__header {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.review_yandex__header img {
  margin-right: 1.25rem;
}

@media (max-width: 991px) {
  .review_yandex__header img {
    margin-bottom: 0.875rem;
  }
}

.review_yandex__header .text {
  max-width: 13.5rem;
  font-family: 'Arial';
  font-size: 1.1875rem;
  line-height: 1.25rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.review_yandex__header .text .icon-top {
  margin-left: 0.125rem;
  margin-bottom: 0.0625rem;
}

@media (max-width: 600px) {
  .review_yandex__header .text {
    flex: 0 0 83.5%;
    max-width: 83.5%;
  }
}

.review_yandex__header .info {
  max-width: 16rem;
  font-family: 'Arial';
  font-size: 0.875rem;
  line-height: 1.125rem;
}

@media (max-width: 600px) {
  .review_yandex__header .info {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 1rem;
  }
}

.review_yandex__control {
  border-radius: 0.5rem;
  display: flex;
  background-color: #F6F6F6;
  padding: 1.5rem 1rem;
}

@media (max-width: 600px) {
  .review_yandex__control {
    justify-content: space-between;
  }
}

.review_yandex__control .left {
  display: flex;
  align-items: center;
  margin-right: 4.5rem;
}

@media (max-width: 1360px) {
  .review_yandex__control .left {
    margin-right: 1.5rem;
  }
}

@media (max-width: 991px) {
  .review_yandex__control .left {
    margin-right: 1rem;
  }
}

.review_yandex__control .left img {
  margin-right: 0.5625rem;
}

.review_yandex__control .left b {
  font-family: 'Arial';
  font-size: 2.5rem;
  line-height: 2rem;
  letter-spacing: -0.0125rem;
}

.review_yandex__control .right {
  max-width: 7.9375rem;
}

@media (max-width: 991px) {
  .review_yandex__control .right {
    display: flex;
    align-items: center;
  }
}

@media (max-width: 600px) {
  .review_yandex__control .right {
    max-width: 6.875rem;
  }
}

.review_yandex__control .right a {
  font-size: 0.875rem;
  line-height: 1rem;
  font-weight: var(--w-roboto-m);
  display: block;
  color: #258FFC;
}

.review > div:last-child {
  margin-right: 0;
}

.icon-top {
  width: 0.8125rem;
  height: 0.8125rem;
  display: inline-block;
  background: url(../img/top.svg) no-repeat;
  background-size: 100%;
}
