.review {
	padding: rem(110) 0;

	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;

	@include min991 {
		flex-wrap: wrap;
	}

	&_google,
	&_yandex,
	&_default {
		flex: 1 1 auto;
		align-self: baseline;
		max-width: rem(400);
		margin-right: rem(24);

		@include min1360 {
			flex: 0 0 calc((100% - 3rem) / 3);
		}

		@include min991 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
			margin-bottom: rem(32);

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&_google {
		border: rem(1) solid #E8EAED;
		padding: rem(24) rem(16);
		border-radius: rem(8);

		@include min991 {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		&__header {
			display: flex;
			// flex-wrap: wrap;
			align-items: center;

			margin-bottom: rem(24);

			@include min991 {
				margin-bottom: 0;
			}

			@include min720 {
				flex: 0 0 100%;
				max-width: 100%;

				margin-bottom: rem(20);
			}

			img {
				width: rem(32);
				height: rem(32);
				margin-right: rem(14);

				@include min991 {
					margin-right: rem(16);
				}

				@include min720 {
					margin-right: rem(14);
				}
			}

			.text {
				font-size: rem(20);
				line-height: rem(24);
				letter-spacing: rem(-0.4);

				@include min1360 {
					max-width: rem(218);
				}

				@include min991 {
					font-size: rem(22);
					max-width: rem(230);
				}

				@include min720 {
					font-size: rem(20);
					max-width: 100%;
				}
				
			}
		}

		&__control {
			display: flex;
			flex-direction: column;

			@include min991 {
				align-items: flex-end;
			}

			@include min720 {
				flex: 0 0 100%;
				max-width: 100%;
				align-items: flex-start;
			}

			&-top {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				margin-bottom: rem(12);

				@include min991 {
					margin-bottom: rem(4);
				}

				span {
					font-weight: var(--w-roboto-m);
					font-size: rem(18);
					line-height: rem(24);
					letter-spacing: rem(-0.7);

					margin-right: rem(12);
				}

				ul {
					display: flex;
					flex-wrap: wrap;
					margin-right: rem(7);

					li {
						margin-right: rem(2.4);

						&:last-child {
							margin-right: 0;
						}
					}
				}

				a {
					font-family: var(--f-open-sans);
					font-weight: var(--w-open-sans-sm);
					color: #3F87E8;
					font-size: rem(14);
					line-height: rem(24);
					letter-spacing: rem(-0.7);
				}
			}

			p {
				font-size: rem(14);
				line-height: rem(24);
				letter-spacing: rem(-0.2);
				color: #72777C;
			}
		}
	}

	&_default {
		border: rem(1) solid #E5EAF1;

		&__top {
			position: relative;
			display: flex;
			// flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			&::before {
				content: "";
				position: absolute;
				bottom: rem(3);
				z-index: -1;
				left: 0;
				width: 100%;
				height: rem(1);
				background-color: #E5EAF1;
			}

			ul {
				display: flex;
				flex-wrap: wrap;
			}

			.text {
				margin-left: rem(23);
				margin-right: rem(40);

				font-family: Arial;
				font-size: rem(14);
				font-weight: bold;

				@include min1360 {
					display: none;
				}

				b {
					color: #8F9BAC;
				}
			}

			.count {
				margin-right: rem(8);

				width: rem(40);
				height: rem(40);
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: #2ABE64;

				font-family: 'Arial';
				color: white;
				font-size: rem(14);
				font-weight: bold;
			}
		}

		&__bottom {
			padding: rem(16);
			a {
				font-family: 'Arial';
				font-weight: bold;
				font-size: rem(15);
				line-height: rem(24);
				color: #2F353F;

				span {
					color: #8F9BAC;
				}
			}
		}
	}

	&_yandex {
		border-radius: rem(14);
		padding: rem(24);
		box-shadow: 0 2px 8px rgba(0,0,0,.2);

		@include min1360 {
			padding: rem(24) rem(18);
		}

		@include min991 {
			display: flex;
			flex-wrap: wrap;

			justify-content: space-between;
		}

		@include min720 {
			padding: rem(16);
		}

		&__header {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: rem(16);

			@include min1360 {
				align-items: flex-start;
			}

			@include min991 {
				flex: 0 0 50%;
				max-width: 50%;

				// align-items: baseline;

				margin-bottom: 0;
			}

			@include min720 {
				flex: 0 0 100%;
				max-width: 100%;
			}

			// @include min560_max640 {
			// 	flex: 0 0 50%;
			// 	max-width: 50%;
			// }

			img {
				margin-right: rem(20);

				@include min1360 {
					// margin-right: rem(17);
					// padding-bottom: rem(12);
				}

				@include min991 {
					margin-bottom: rem(14);
				}
			}

			.text {
				// position: relative;
				max-width: rem(216);
				font-family: 'Arial';
				font-size: rem(19);
				line-height: rem(20);
				font-weight: bold;

				margin-bottom: rem(16);

				.icon-top {
					margin-left: rem(2);
					margin-bottom: rem(1);
				}

				@include min720 {
					flex: 0 0 83.5%;
					max-width: 83.5%;
				}
			}

			.info {
				// margin-bottom: rem(16);
				max-width: rem(256);

				font-family: 'Arial';
				font-size: rem(14);
				line-height: rem(18);

				@include min720 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(16);
				}

				// @include min560_max720 {
					
				// }
			}
		}

		// &__text {
		// 	margin-bottom: rem(16);
		// 	max-width: rem(256);

		// 	p {
		// 		font-family: 'Arial';
		// 		font-size: rem(14);
		// 		line-height: rem(18);

				
		// 	}
		// }

		&__control {
			border-radius: rem(8);
			display: flex;
			// flex-wrap: wrap;
			background-color: #F6F6F6;

			padding: rem(24) rem(16);

			// @include min991 {
			// 	flex: 0 0 50%;
			// 	max-width: 50%;
			// }

			@include min720 {
				justify-content: space-between;
			}

			// @include min560_max640 {
			// 	flex: 0 0 50%;
			// 	max-width: 50%;
			// }

			.left {
				display: flex;
				// flex-wrap: wrap;
				align-items: center;
				margin-right: rem(72);

				@include min1360 {
					margin-right: rem(24);
				}

				@include min991 {
					margin-right: rem(16);
				}

				img {
					margin-right: rem(9);
				}

				b {
					font-family: 'Arial';
					font-size: rem(40);
					line-height: rem(32);
					letter-spacing: rem(-0.2);
				}
			}

			.right {
				max-width: rem(127);

				@include min991 {
					display: flex;
					align-items: center;
				}

				@include min720 {
					// justify-content: flex-end;
					max-width: rem(110);
				}

				a {
					font-size: rem(14);
					line-height: rem(16);
					font-weight: var(--w-roboto-m);
					display: block;
					color: #258FFC;
				}
			}
		}
	}

	> div {
		&:last-child {
			margin-right: 0;
		}
	}
}

.icon-top {
	width: rem(13);
	height: rem(13);
	display: inline-block;
	
	background: url(../img/top.svg) no-repeat;
	background-size: 100%;
}
